@import "../../../shared/styling/Base.scss";

.modal{
    display:flex;
    flex-direction: column;
    gap:1rem;
    position: absolute;
    z-index: 1;
    width: 25rem;
    height:15rem;
    background-color: white;
    top:50%;
    left:50%;
    transform: translateX(-50%);
    border-radius: $rounded-corners;
    text-align: center;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);

    .title{
        font-size: $txt-m;
    }

    .button{
        margin:0 auto;
    }
}

.backdrop{
    height:100%;
    width:100vw;
    background-color: rgba(69, 68, 68, 0.478);
    position: absolute;
}